.player-wrapper {
  position: relative;
  padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
  margin-top: 0.1%;
  margin-left: 0.3%;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.title {
  text-align: left;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  color: whitesmoke;
  font-weight: bold;
  font-size: 2rem;
  margin-bottom: -1rem;
}

.list-inline-item {
  color: rgb(226, 208, 208);
  margin-bottom: -1rem;
}

.tagline {
  margin-top: '-155rem';
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 1rem;
  color: grey;
}

h4 {
  line-height: 2;
  color: whitesmoke;
}
.overview {
  color: #fff;
  font-size: 0.5rem;
  font-family: "Source Sans Pro", Arial, sans-serif;
}
